.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

form {
  display: flex;
  flex-direction: column;
  gap: 30px;
  align-items: center;
  width: 80vw;
}

input, textarea {
  /* width: 300px; */
  background-color: hsl(220, 13%, 30%);
  padding: 10px;
  border: none;
  border-radius: 10px;
  color: white;
}

input {
  text-align: center;
  font-size: calc(10px + 2vmin);
}

textarea {
  font-size: calc(10px + 1vmin);
  max-width: 500px;
  width: 100%;
  resize: none;
}

input:focus, input:hover, textarea:focus, textarea:hover {
  outline: none;
}

button {
  width: 50%;
  max-width: 150px;
  background-color: #282c34;
  border: 2px solid #00B8EA;
  border-radius: 10px;
  padding: 10px;
  color: white;
  font-family : inherit;
  font-size: calc(10px + 1vmin);
  transition: 0.3s;
}

button:hover {
  background-color: hsl(193, 100%, 46%);
  cursor: pointer;
}

form span {
  font-size: calc(10px + 1vmin);
  color: hsl(0, 70%, 50%);
}

.hidden {
  display: none;
}

table {
  table-layout: fixed;
  width: 80vw;
  border-collapse: collapse;
  border-spacing: 5px 1rem;
  margin-bottom: 50px;
}

tr {
  border-bottom: 1px solid #fff5;
}

td {
  padding: 20px;
  font-size: calc(10px + 1vmin);
}

h3 {
  margin-top: 100px;
}